<template>
  <div class="copy">
    <div class="area" :style="getHorizontalStyle">
      <button class="cancle" @click="cancle"></button>
      <div class="canvas-box">
        <div class="swiper" ref="copySwiper">
          <div class="swiper-prev">
            <div @click="swiperPrev" :class="{'swiperDisable':num === 0}">〈</div>
          </div>
          <div class="swiper-box">
            <div class="swiper-slice" v-for="(i, index) in list" :key="index">
              <canvas :id="'canvas' + i" class="canvas"></canvas>
            </div>
          </div>
          <div class="swiper-next">
            <div @click="swiperNext" :class="{'swiperDisable':num === texts.length-1}">〉</div>
          </div>
        </div>
      </div>

      <!-- <div class="pagenation">{{ num + 1 }}/25</div> -->
      <div class="btn">
        <a href="javascript:;" @click="reset">重置</a>
        <a href="javascript:;" :class="{'subDisabled': drawList.length!==25}" @click="confirm">提交</a>
      </div>
      <div class="text-list">
        <ul class="text-box">
          <li
            v-for="(text,index) in texts"
            :key="index"
            :id="'text_' + index"
            class="text"
            :class="{'zoom': num === index, 'activeText': drawList[index] && drawList[index].isDraw}"
            @click="onClick(index)"
          >{{text}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import Draw from "@/common/draw";
export default {
  name: "Copy",
  data() {
    return {
      pdfUrl: "",
      degree: 90,
      num: 0,
      list: [],
      length: 25,
      drawList: [],
      imgList: [],
      disabled: true,
      texts: [
        "保",
        "险",
        "人",
        "已",
        "明",
        "确",
        "说",
        "明",
        "免",
        "除",
        "保",
        "险",
        "人",
        "责",
        "任",
        "条",
        "款",
        "的",
        "内",
        "容",
        "及",
        "法",
        "律",
        "后",
        "果"
      ]
    };
  },
  props: {
    url: {
      type: String,
      require: false
    }
  },
  methods: {
    reset() {
      this.drawList[this.num].clear();
    },
    confirm() {
      if (this.drawList.length === 25) {
        this.$emit("submit", this.drawList);
      }
    },
    /*
     * 在签名区域增加底纹（预设的姓名），通过翻页的方式签名
     */
    initCanvas(num) {
      var canvas = document.getElementById("canvas" + num);
      if (!this.drawList[num]) {
        this.drawText(canvas, this.texts[num], canvas.width, canvas.height);
        var draw = new Draw(canvas, -this.degree, {
          lineWidth: 4
        });
        this.drawList[num] = draw; // 存放所有声明文字对象
      }
    },
    cancle() {
      // 关闭弹窗
      this.$emit("close");
    },
    onClick(index) {
      if (this.drawList[index] && this.drawList[index].isDraw) {
        this.num = index;
        this.change(this.num);
      }
    },
    swiperPrev() {
      // 往前翻页
      this.num = this.num - 1;
      if (this.num > -1) {
        this.change(this.num);
      } else {
        this.num = 0;
      }
    },
    swiperNext() {
      // 往后翻页
      if (this.drawList[this.num].isDraw) {
        this.num = this.num + 1;
        if (this.num < this.length) {
          this.change(this.num);
          this.initCanvas(this.num);
        } else {
          this.num = this.length - 1;
        }
      } else {
        this.$toast(
          `请在方格内手写录入【${this.texts[this.num]}】`,
          "error",
          2000,
          document.getElementsByClassName("area")[0]
        );
      }
    },
    change(num) {
      var swiper = document.getElementsByClassName("swiper-box")[0];
      var swiperSlice = document.getElementsByClassName("swiper-slice")[0];
      swiper.style.left = num * -swiperSlice.offsetWidth + "px";
    },
    generateArray(start, end) {
      return Array.from(new Array(end + 1).keys()).slice(start);
    },
    drawText(canvasEle, text, width, height) {
      // 给田字格添加底纹
      var canvas = document.createElement("canvas");
      canvas.width = width;
      canvas.height = width;
      var context = canvas.getContext("2d");
      var img = new Image();
      img.src = require("../assets/timg.jpg");
      img.onload = () => {
        img.width = width;
        img.height = height;
        context.drawImage(img, 0, 0, width, width);
        // 设置字体
        context.font = "11rem 宋体";
        // 设置颜色
        context.fillStyle = "#aaaaaa";
        context.textAlign = "center";
        context.textBaseline = "middle";
        // 绘制文字（参数：要写的字，x坐标，y坐标）
        context.fillText(text, img.width / 2, img.height);
        var bg = canvas.toDataURL("image/jpeg", 0.3);

        canvasEle.style.backgroundImage = "url(" + bg + ")";
        canvasEle.style.backgroundSize = "100% 100%";
      };
    },
    initElement() {
      var swiperWidth = this.$refs.copySwiper.offsetWidth;
      console.log(swiperWidth);
      var swiperPrev = document.getElementsByClassName("swiper-prev")[0];
      var swiperNext = document.getElementsByClassName("swiper-next")[0];
      swiperPrev.style.width = swiperWidth * 0.1 + 'px';
      swiperNext.style.width = swiperWidth * 0.1 + 'px';
      var swiperSlices = document.getElementsByClassName("swiper-box")[0].children;

      for(var i = 0; i < swiperSlices.length; i++) {
        var slice = swiperSlices[i];
        slice.style.width = swiperWidth * 0.8 + 'px';
        slice.style.padding = '0 ' + swiperWidth * 0.1 + 'px';
        var canvas = slice.querySelector("canvas");
        canvas.style.width = swiperWidth * 0.8 + 'px';
      }
    }
  },
  computed: {
    getHorizontalStyle() {
      const d = document;
      const w =
        window.innerWidth ||
        d.documentElement.clientWidth ||
        d.body.clientWidth;
      const h =
        window.innerHeight ||
        d.documentElement.clientHeight ||
        d.body.clientHeight;
      let length = (h - w) / 2;
      let width = w;
      let height = h;

      switch (this.degree) {
        case -90:
          length = -length;
          break;
        case 90:
          width = h;
          height = w;
          break;
        default:
          length = 0;
      }

      if (this.canvasBox) {
        this.canvasBox.removeChild(document.querySelector("canvas"));
        this.canvasBox.appendChild(document.createElement("canvas"));
        // setTimeout(() => {
        //   this.initCanvas();
        // }, 400);
      }
      return {
        transform: `rotate(${this.degree}deg) translate(0px,${length}px)`,
        width: `${width}px`,
        height: `${height}px`,
        transformOrigin: "center center"
      };
    }
  },
  mounted() {
    this.list = this.generateArray(0, this.length);
    this.canvasBox = document.getElementsByClassName("canvas-box")[0];
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
li {
  display: inline-block;
}
.text-list {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 70px;
  overflow: hidden;
}
.text-box {
  white-space: nowrap;
  height: 70px;
  overflow: auto;
  box-sizing: border-box;
  border: 1px solid #cccccc;
}
.text-box .text {
  width: 70px;
  height: 70px;
  line-height: 70px;
  /* float: left; */
  background: #fff;
  color: #aaaaaa;
  border-right: 1px solid #ccc;
  transition: transform 0.5s;
}
.text-box .text:last-child {
  border: none;
}
.copy {
  width: 100%;
  height: 100%;
}
.cancle {
  width: 60px;
  height: 60px;
  display: block;
  background: url(../assets/cancle.png) no-repeat;
  background-size: 100% 100%;
  border: 0;
  position: absolute;
  right: 0;
  top: 20px;
  z-index: 200;
}
.area {
  /* width: 630px;
  height: 570px; */
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background: #efebb9;
  overflow: hidden;

  /* display: flex;
  flex-direction: row; */
}
.canvas-box {
  /* margin: 0 25px; */
  margin-top: 100px;
  height: 590px;
  overflow: hidden;
  width:100%;
}
#canvas {
  width: 100%;
  height: 100%;
  /* cursor: crosshair; */
}
.canvas {
  width: 100%;
  height: 550px;
  margin-top: 40px;
  background: url(../assets/timg.jpg);
  background-repeat: no-repeat;
  background-size: 100% 550px;
  /* cursor: crosshair; */
}
.btn {
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: #efebb9;
  position: absolute;
  top: 20px;
  left: 0;
}
.btn a {
  display: block;
  width: 162px;
  height: 62px;
  line-height: 62px;
  color: #643b1c;
  background: url(../assets/btn_bg.png) no-repeat;
  background-size: 100% 100%;
  font-size: 32px;
  text-align: center;
  text-decoration: none;
}
.swiper {
  position: relative;
  overflow: hidden;
  height: 100%;
  width: 80%;
  margin: 0 auto;
}
.swiper-box {
  width: 10000%;
  height: 100%;
  position: absolute;
  left: 0;
}
.swiper-slice {
  width: 0.8%;
  float: left;
  padding: 0 0.1%;
}
.swiper-prev {
  width: 0.1%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  line-height: 100%;
  margin: 0 auto;
  z-index: 1;
}
.swiper-prev div {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 60px;
}
.swiper-next {
  width: 0.1%;
  height: 100%;
  position: absolute;
  right: 0;
  top: 0;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  line-height: 100%;
  margin: 0 auto;
  z-index: 1;
}
.swiper-next div {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 60px;
}
.swiperDisable {
  cursor: not-allowed;
  color: #aaaaaa;
}
.text-box .activeText {
  color: #000;
  font-weight: bold;
}
.zoom {
  transform: scale(1.3);
  border: 1px solid #ccc;
}
.subDisabled {
  background: #ccc !important;
  color: #fff !important;
}
</style>
