import utils from "../common/utils";
import Draw from "@/common/draw";
import copy from "./Copy.vue";
import axios from "axios";
export default {
  name: "Index",
  components: {
    copy
  },
  data() {
    return {
      isNeedCopy: true,
      copyDialog: false,
      datas: [
        // {
        //   id: "10001",
        //   name: "安诚车险投保单.pdf",
        //   url: "/pdf_viewer/web/viewpdf.pdf", // /ElectronicPolicy/d/s/0I5sTU/1
        //   isRead: false
        // }
      ],
      submitDisabled: true,
      pdfDialog: false,
      degree: 90,
      busiCode: "",
      maxtime: 3,
      timeSecond: 3,
      webRoot: process.env.VUE_APP_BASE_PATH,
      path: process.env.VUE_APP_API + "pcis/electricSign",
      btnDisabled: true,
      pdfUrl: "",
      disable: true,
      showStatement: false,
      showAgree: false,
      copyImg: null,
      disableAgree: true,
      signText: "",
      list: [],
      signNum: 0,
      drawSignList: [],
      ocrApiCount: 0,
      idNumber: null,
      isShowIdentify: true,
      errorMessage: "",
      buffer: []
    };
  },
  methods: {
    onAgree() {
      var isAllRead = true;
      for (var i = 0; i < this.datas.length; i++) {
        if (!this.datas[i].isRead) {
          isAllRead = false;
          break;
        }
      }

      if (!isAllRead) {
        this.$toast("请先阅读完以上PDF", "error", 2000);
      }
    },
    copy() {
      if (this.showAgree) {
        var agreeEle = document.getElementById("agreeCheck");
        if (agreeEle.checked) {
          this.copyDialog = true;
          setTimeout(() => {
            this.$refs.copy.initElement();
            this.$refs.copy.initCanvas(this.$refs.copy.num);
          }, 200);
        } else {
          this.$toast("请先勾选阅读须知", "error", 2000);
        }
      } else {
        var isAllRead = true;
        for (var i = 0; i < this.datas.length; i++) {
          if (!this.datas[i].isRead) {
            isAllRead = false;
            break;
          }
        }

        if (isAllRead) {
          this.copyDialog = true;
          setTimeout(() => {
            this.$refs.copy.initElement();
            this.$refs.copy.initCanvas(this.$refs.copy.num);
          }, 200);
        } else {
          this.$toast("请先阅读完以上PDF", "error", 2000);
        }
      }
    },
    initCanvas(num) {
      var canvas;
      console.log(this.$refs.dialogsign);
      var singer = document.getElementById("signerBox");
      if (this.signText.length > 0) {
        this.ocrApiCount = 0;
        canvas = document.getElementById("signcanvas" + num);
        if (!this.drawSignList[num]) {
          this.drawSignText(
            canvas,
            this.signText.charAt(num),
            canvas.width,
            canvas.height
          );
          var draw = new Draw(canvas, 0, {
            lineWidth: 10
          });
          this.drawSignList[num] = draw; // 存放所有签名对象
        }
      } else {
        canvas = singer.querySelector("canvas");
        if (!this.draw) {
          this.draw = new Draw(canvas, -this.degree, {
            lineWidth: 4
          });
        }
      }
    },
    sign() {
      if (this.showStatement) {
        if (this.$refs.copy.drawList.length === 25) {
          setTimeout(() => {
            this.$refs.dialogsign.style.display = "block";
            setTimeout(() => {
              this.initCanvas(this.signNum);
            }, 400);
          }, 500);
        } else {
          this.$toast("请先进行手写抄录", "error");
        }
      } else {
        if (this.showAgree) {
          var agreeEle = document.getElementById("agreeCheck");
          if (agreeEle.checked) {
            this.$refs.dialogsign.style.display = "block";
            setTimeout(() => {
              this.initCanvas(this.signNum);
            }, 400);
          } else {
            this.$toast("请先勾选阅读须知", "error", 2000);
          }
        } else {
          var isAllRead = true;
          for (var i = 0; i < this.datas.length; i++) {
            if (!this.datas[i].isRead) {
              isAllRead = false;
              break;
            }
          }
          if (isAllRead) {
            setTimeout(() => {
              this.$refs.dialogsign.style.display = "block";
              setTimeout(() => {
                this.initCanvas(this.signNum);
              }, 400);
            }, 500);
          } else {
            this.$toast("请先阅读完以上PDF", "error", 2000);
          }
        }
      }
    },
    onSubmit() {
      console.log(this.copyImg);
      if (this.signText.length > 0) {
        this.getBase64Image(
          this.drawSignList, {
            width: 360,
            height: 360
          },
          url => {
            this.commitImage(url);
          }
        );
      } else {
        this.commitImage(this.draw.getPNGImage());
      }
    },
    commitImage(signImg) {
      this.$$api
        .post(this.path, {
          requestType: "03",
          busiCode: this.busiCode,
          copyImg: this.copyImg,
          signImg: signImg
        })
        .then(data => {
          if (data === "") {
            this.$router.push("/success");
          } else {
            window.location.href = data;
          }
        });
    },
    copySubmit(drawList) {
      this.copyDialog = false;
      var imgs = "";
      for (var i = 0; i < drawList.length; i++) {
        var draw = drawList[i];
        imgs +=
          '<img style="width:40px;display:block;" src="' +
          draw.getPNGImage() +
          '"/>';
      }
      var _this = this;
      this.getBase64Image(
        drawList, {
          width: 40,
          height: 28
        },
        function (img) {
          _this.copyImg = img;
        }
      );
      this.$refs.copyImg.innerHTML = imgs;
    },
    onNotice(data, idx) {
      this.token = utils.getGenerateUUID();
      this.pdfId = data.id;
      this.pdfDialog = true;
      var url = data.url;
      // if (process.env.NODE_ENV === "development") {
      //   url = data.url.replace("https://plytst.e-acic.com", "");
      //   url = "/ppi" + url;
      // }
      console.log(url);
      this.pdfUrl = encodeURIComponent(url);
      var timer = setInterval(() => {
        var iframe = document.getElementsByTagName("iframe")[0];
        if (iframe) {
          var ifrDoc = iframe.contentWindow.document;
          if (!ifrDoc || !ifrDoc.getElementById("pageNumber")) {
            return;
          }
          var pdfIsReady =
            iframe.contentWindow.PDFViewerApplication.pdfViewer.pageViewsReady;
          console.log(pdfIsReady);
          if (pdfIsReady) {
            clearInterval(timer);
            this.currentIdx = idx;
            if (!this.datas[idx].isRead) {
              this.btnDisabled = true;
              this.maxtime = this.timeSecond;
              this.timer = setInterval(this.countDown, 1000);
            } else {
              this.btnDisabled = false;
              this.$nextTick(() => {
                this.$refs.countdown.style.display = "none";
              });
            }
            this.$$api
              .post(this.path, {
                requestType: "02",
                busiCode: this.busiCode,
                id: this.pdfId,
                flag: "start",
                token: this.token
              })
              .then(data => {
                console.log(data);
              });
          }
        }
      }, 200);
    },
    cancleSign() {
      this.$refs.dialogsign.style.display = "none";
    },
    reset() {
      // 重置签名
      if (this.signText.length > 0) {
        this.drawSignList[this.signNum].clear();
      } else {
        this.draw.clear();
      }
    },
    // 提交签名，进行ocr验证
    confirm() {
      var imgs = "";
      var imgsList = [];
      const canvas = document.getElementsByClassName("signer")[0];
      if (this.draw && this.draw.isDraw) {
        imgs =
          '<img style="width:' +
          canvas.offsetWidth / 2.5 +
          'px;display:block;margin:0 auto;height:100%;" src="' +
          this.draw.getPNGImage() +
          '">';
        this.$refs.signImg.innerHTML = imgs;
        this.$refs.dialogsign.style.display = "none";
        this.disable = false;
      } else if (this.signText.length > 0) {
        var sWidth = document.getElementsByClassName("signature")[0]
          .offsetWidth;
        var iWidth = sWidth ?
          (sWidth / this.signText.length) * 0.8 :
          canvas.offsetWidth / 2.5;
        if (
          iWidth > document.getElementsByClassName("signature")[0].offsetHeight
        ) {
          iWidth = document.getElementsByClassName("signature")[0].offsetHeight;
        }
        for (var i = 0; i < this.drawSignList.length; i++) {
          imgsList[i] = this.drawSignList[i].getPNGImage();
          imgs +=
            '<img style="width:' +
            iWidth +
            'px;" src="' +
            this.drawSignList[i].getPNGImage() +
            '">';
        }
        if (this.ocrApiCount < 3) {
          this.ocrApiCount++;
          this.getBase64Image(
            this.drawSignList, {
              width: 360,
              height: 360
            },
            url => {
              // console.log(url);
              this.$$api
                .rpost(process.env.VUE_APP_API + "CHANBaiduOCR/servlet/OCR", {
                  businessNo: this.busiCode ? this.busiCode : null,
                  content: [{
                    index: 0,
                    name: this.signText ? this.signText : null,
                    handwriting: url ? url : null
                  }]
                })
                .then(res => {
                  console.log(res);
                  if (res && res !== "") {
                    var result = res;
                    var messages = document.getElementsByClassName("ocrMes");
                    var masks = document.getElementsByClassName(
                      "swiper-slice-mask"
                    );
                    var isClose = true;
                    for (var r = 0; r < result.split(",").length; r++) {
                      var resp = result.split(",")[r];
                      var mes = "";
                      if (resp === "1") {
                        mes = "校验成功";
                        masks[r].style.display = "block";
                      } else {
                        mes = "校验失败";
                        isClose = false;
                        masks[r].style.display = "none";
                      }
                      messages[r].innerText = mes;
                    }
                    if (isClose) {
                      this.$refs.signImg.innerHTML = imgs;
                      this.$refs.dialogsign.style.display = "none"; // 关闭弹窗
                      this.disable = false;
                    }
                  }
                })
                .catch(error => {
                  console.log(error);
                });
            }
          );
        } else {
          // 三次ocr失败跳过ocr
          this.getBase64Image(
            this.drawSignList, {
              width: 360,
              height: 360
            },
            url => {
              this.$$api
                .post(this.path, {
                  requestType: "03",
                  busiCode: this.busiCode,
                  ocrFlag: 0,
                  signImg: url
                })
                .then(data => {
                  console.log(data);
                });
            }
          );

          this.$refs.signImg.innerHTML = imgs;
          this.$refs.dialogsign.style.display = "none";
          this.disable = false;
        }
      } else {
        this.$toast(
          "请先进行手写签名",
          "error",
          2000,
          document.getElementById("signArea")
        );
      }
    },

    close() {
      this.copyDialog = false;
    },
    countDown() {
      if (this.maxtime > 0) {
        --this.maxtime;
        var msg = "(" + this.maxtime + "s)";
        this.$refs.countdown.innerText = msg;
      } else {
        clearInterval(this.timer);
        this.btnDisabled = false;
        this.$refs.countdown.style.display = "none";
      }
    },
    end() {
      var iframe = document.getElementsByTagName("iframe")[0];
      var ifrDoc = iframe.contentWindow.document;
      var currentPage = ifrDoc.getElementById("pageNumber").value;
      var totalPage = ifrDoc.getElementById("numPages").innerText.substring(2);
      // iframe.contentWindow.loc   if (totalPage !== "0") {
      if (currentPage === totalPage) {
        this.pdfDialog = false;
        this.datas[this.currentIdx].isRead = true;
        if (this.currentIdx === this.datas.length - 1) {
          this.disableAgree = false;
        }
        this.$$api
          .post(this.path, {
            requestType: "02",
            busiCode: this.busiCode,
            id: this.pdfId,
            flag: "end",
            token: this.token
          })
          .then(data => {
            console.log(data);
          });
      } else {
        this.$toast("请先阅读完以上内容", "error", 2000);
      }
    },
    drawSignText(canvasEle, signText, width, height) {
      // 给田字格添加底纹
      var canvas = document.createElement("canvas");
      canvas.width = width;
      canvas.height = width;
      var context = canvas.getContext("2d");
      var img = new Image();
      img.src = require("../assets/timg.jpg");
      img.onload = () => {
        img.width = width;
        img.height = height;
        context.drawImage(img, 0, 0, width, width);
        // 设置字体
        // var fontsize = document
        //   .getElementsByTagName("html")[0]
        //   .style.fontSize.replace("px", "");
        // console.log(fontsize);
        var fontsize = 32;
        context.font = fontsize * 8 + "px 宋体";
        // content.font = '6rem 宋体';
        // 设置颜色
        context.fillStyle = "#ccc";
        context.textAlign = "center";
        context.textBaseline = "middle";
        // 绘制文字（参数：要写的字，x坐标，y坐标）
        context.fillText(signText, img.width / 2, img.height);
        var bg = canvas.toDataURL("image/png");
        canvasEle.style.backgroundImage = "url(" + bg + ")";
        canvasEle.style.backgroundSize = "100% 100%";
      };
    },
    swiperPrev() {
      // 往前翻页
      this.signNum = this.signNum - 1;
      if (this.signNum > -1) {
        this.change(this.signNum);
      } else {
        this.signNum = 0;
      }
    },
    swiperNext() {
      // 往后翻页
      this.signNum = this.signNum + 1;
      if (this.signNum < this.signText.length) {
        this.change(this.signNum);
        this.initCanvas(this.signNum);
      } else {
        this.signNum = this.signText.length - 1;
      }
    },
    change(num) {
      var swiper = document.getElementById("sign-swiper-box");
      var swiperSlice = document.getElementsByClassName("sign-swiper-slice")[0];
      swiper.style.left = num * -swiperSlice.offsetWidth + "px";
    },
    // 合成抄录图片
    getBase64Image(content, size, callback) {
      var loadImg = function (url) {
        return new Promise(resolve => {
          // 创建图片
          var img = new Image();
          // 加载成功
          img.src = url;
          img.onload = () => {
            resolve(img);
          };
        });
      };
      var canvansImg = document.createElement("canvas");
      var ctx = canvansImg.getContext("2d");
      canvansImg.width = size.width * content.length;
      canvansImg.height = size.height;
      var ImgAsync = async function (content) {
        for (var i = 0; i < content.length; i++) {
          let img = await loadImg(content[i].getPNGImage());
          ctx.fillStyle = "#FFFFFF";
          ctx.fillRect(size.width * i, 0, size.width, size.height);
          ctx.drawImage(img, size.width * i, 0, size.width, size.height);
          ctx.fill();
        }
        return new Promise(res => {
          res(canvansImg.toDataURL("image/jpeg"));
        });
      };

      ImgAsync(content).then(function (img) {
        callback(img);
      });
    },
    beginVideo() {
      this.$refs.file.click();
    },
    //上传视频，进行人脸识别验证
    onFileChange(e) {
      console.log(e.target.files[0]);
      if (e.target.files[0] == null || e.target.files[0] == "undefined") {
        return;
      }
      if (e.target.files[0].size / 1024 / 1024 > 6) {
        this.$toast("上传视频超过6M，请重新上传!", "error", 2000);
        return;
      }
      var data = new FormData();
      data.append("vedio-file", e.target.files[0]); //视频文件
      data.append("busiCode", this.busiCode); //业务id
      data.append("name", this.signText); // 名字
      data.append("idNumber", this.idNumber); //证件号
      this.$refs.load.style.display = "block";
      axios({
          url: "/FaceRecognition/servlet/face",
          method: "post",
          data: data,
          headers: {
            "Content-Type": "multipart/form-data"
          }
        })
        .then(resp => {
          console.log(resp);
          this.$refs.load.style.display = "none";
          if (resp.data.code === "1000") {
            this.isShowIdentify = false;
            this.getList();
          } else {
            if (
              resp.data.code !== "1" &&
              resp.data.code !== "3003" &&
              resp.data.code !== "3004" &&
              resp.data.code !== "3005"
            ) {
              this.$toast(`系统错误！错误码：${resp.data.code}`, "error", 2000);
            } else {
              this.errorMessage = resp.data.message;
            }
          }
        })
        .catch(e => {
          console.log(e);
          this.$toast(`系统错误！`, "error", 2000);
          this.$refs.load.style.display = "none";
        });
    },
    getFormatUrl(url, paramName) {
      var params = url.split("?")[1];
      if (params) {
        for (var k of params.split("&")) {
          var key = k.split("=")[0];
          var value = k.split("=")[1];
          if (key === paramName) {
            return decodeURIComponent(value);
          }
        }
      }
      return null;
    },
    generateArray(start, end) {
      return Array.from(new Array(end + 1).keys()).slice(start);
    },
    getList() {
      this.$$api
        .npost(this.path, {
          requestType: "01",
          busiCode: this.busiCode
        })
        .then(data => {
          for (var i = 0; i < data.length; i++) {
            data[i].isRead = false;
          }
          this.datas = data;
        });
    },
    gotMediaStream(stream) {
      window.stream = stream;
      this.$refs.video.srcObject = stream;
    },
    handleDataAvailable(e) {
      if (e && e.data && e.data.size > 0) {
        this.buffer.push(e.data);
      }
    },
    handleError(err) {
      console.log("getUserMedia:", err);
    },
    stopRecord() {
      this.mediaRecorder.stop();
    },
    start() {
      if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
        console.log("getUserMedia is not supported");
      } else {
        var constraints = {
          video: {
            width: 480,
            height: 480,
            frameRate: 15
          },
          audio: false
        };
        navigator.mediaDevices
          .getUserMedia(constraints)
          .then(this.gotMediaStream)
          .catch(this.handleError);
      }
    },
    startRecord() {
      this.start();
    }
  },
  mounted() {
    this.busiCode = this.getFormatUrl(window.location.href, "busiCode");
    this.showStatement = this.getFormatUrl(
      window.location.href,
      "showStatement"
    );
    if (this.showStatement === "true") {
      this.showStatement = true;
    } else {
      this.showStatement = false;
    }
    this.showAgree = this.getFormatUrl(window.location.href, "showAgree");
    if (this.showAgree === "true") {
      this.showAgree = true;
    } else {
      this.showAgree = false;
    }
    // 如果没有传idNumber不进入人脸识别，直接进入阅读签字页面
    if (!this.idNumber) {
      this.isShowIdentify = false;
      this.getList();
    } else {
      this.isShowIdentify = true;
    }

    this.canvasBox = document.getElementsByClassName("canvas-box")[0];
  },
  created() {
    this.idNumber = this.getFormatUrl(window.location.href, "IdNumber");
    this.signText = this.getFormatUrl(window.location.href, "signName");
    this.signText = this.signText ? this.signText : "";
    this.list = this.generateArray(0, this.signText.length - 1);
  },
  computed: {
    getHorizontalStyle() {
      if (this.signText.length === 0) {
        const d = document;
        const w =
          window.innerWidth ||
          d.documentElement.clientWidth ||
          d.body.clientWidth;
        const h =
          window.innerHeight ||
          d.documentElement.clientHeight ||
          d.body.clientHeight;
        let length = (h - w) / 2;
        let width = w;
        let height = h;

        switch (this.degree) {
          case -90:
            length = -length;
            break;
          case 90:
            width = h;
            height = w;
            break;
          default:
            length = 0;
        }
        var canvasBox = document.getElementsByClassName("canvas-box")[0];
        if (canvasBox) {
          canvasBox.removeChild(document.querySelector("canvas"));
          canvasBox.appendChild(document.createElement("canvas"));
        }
        return {
          transform: `rotate(${this.degree}deg) translate(0px,${length}px)`,
          width: `${width}px`,
          height: `${height}px`,
          transformOrigin: "center center"
        };
      }
      return "";
    }
  }
};